import React from 'react';
import { arrayOf, string, shape } from 'react-proptypes';

import { Button } from '../../../ui';
import './Features.scss';
import Link from 'gatsby-link';

const Feature = ({ title, icon, color, text }, index) => (
    <div key={index}>
        <div className="feature__container">
            <i className="material-icons"
                style={{ color }}>
                {icon}
            </i>

            <h4>
                {title}
            </h4>

            <p className="copytext-small">
                {text}
            </p>
        </div>
    </div>
);

const Features = ({ params }) => (
    <div className="features__container">
        <div className="features__background"
            style={{ backgroundImage: `url(${process.env.ASSETS_URL}backgrounds/features-background.png)` }} />

        <div className="features__content">
            <div className="features__items">
                {
                    params.items.map((item, index) => Feature(item, index))
                }
            </div>
        </div>

        <div className="features__link">
            <Link
                to={'/skills'}>
                <Button
                    primary
                    outline >
                    Learn more
                </Button>
            </Link>
        </div>
    </div>
);

Features.propTypes = {
    items: arrayOf(shape({
        title: string,
        icon: string,
        color: string,
        text: string
    }))
};

Features.defaultProps = {
    items: []
};

export default Features;



